.dark .media-manager{
  background-color:#212837;
  background-color:var(--media-bg-color-dark, #212837);
  border-color:#364152;
  border-color:var(--media-border-color-dark, #364152)
}
.dark .media-manager .item{
  background-color:#27303F;
  background-color:var(--media-item-bg-color-dark, #27303F);
  border-color:#364152;
  border-color:var(--media-item-border-color-dark, #364152)
}
.dark .media-manager .item:hover{
  background-color:#364152;
  background-color:var(--media-item-hover-bg-color-dark, #364152)
}
.dark .media-manager .item.selected{
  background-color:#2F3949;
  background-color:var(--media-item-selected-bg-color-dark, #2F3949);
  border-color:#2b6cb0;
  border-color:var(--media-item-selected-border-color-dark, #2b6cb0)
}
.dark .media-manager .breadcrumbs{
  background-color:#27303F;
  background-color:var(--media-breadcrumbs-bg-color-dark, #27303F);
  border-color:#364152;
  border-color:var(--media-breadcrumbs-border-color-dark, #364152)
}
.dark .media-manager .sidebar{
  background-color:#27303F;
  background-color:var(--media-sidebar-bg-color-dark, #27303F);
  border-color:#364152;
  border-color:var(--media-sidebar-border-color-dark, #364152)
}
.dark .media-manager .dragdrop{
  background-color:#212837;
  background-color:var(--media-bg-color-dark, #212837)
}
.dark .media-manager .loading{
  background-color:#212837;
  background-color:var(--media-bg-color-dark, #212837)
}
.media-manager{
  background-color:#fff;
  background-color:var(--media-bg-color, #fff);
  border-color:#CFD8E3;
  border-color:var(--media-border-color, #CFD8E3)
}
.media-manager .item{
  background-color:#F1F5F9;
  background-color:var(--media-item-bg-color, #F1F5F9);
  border-color:#CFD8E3;
  border-color:var(--media-item-border-color, #CFD8E3)
}
.media-manager .item:hover{
  background-color:#E2E8F0;
  background-color:var(--media-item-hover-bg-color, #E2E8F0)
}
.media-manager .item.selected{
  background-color:#D9E0EA;
  background-color:var(--media-item-selected-bg-color, #D9E0EA);
  border-color:#90cdf4;
  border-color:var(--media-item-selected-border-color, #90cdf4)
}
.media-manager .breadcrumbs{
  background-color:#F1F5F9;
  background-color:var(--media-breadcrumbs-bg-color, #F1F5F9);
  border-color:#CFD8E3;
  border-color:var(--media-breadcrumbs-border-color, #CFD8E3)
}
.media-manager .sidebar{
  background-color:#fff;
  background-color:var(--media-sidebar-bg-color, #fff);
  border-color:#CFD8E3;
  border-color:var(--media-sidebar-border-color, #CFD8E3)
}
.media-manager .dragdrop{
  background-color:#fff;
  background-color:var(--media-bg-color, #fff)
}
.media-manager .loader{
  background-color:#fff;
  background-color:var(--media-bg-color, #fff)
}