@keyframes scale-x-254ca3ef{
0%{
    transform:scaleX(1)
}
100%{
    transform:scaleX(0)
}
}
@keyframes indeterminate-254ca3ef{
0%{
    width:30%;
    left:-40%
}
50%{
    left:100%;
    width:100%
}
to{
    left:100%;
    width:0
}
}
.progress_bar[data-v-254ca3ef]{
  border-radius:0.25rem;
  transform-origin:left;
  animation:scale-x-254ca3ef linear 1 forwards
}
.progress_bar_indeterminate[data-v-254ca3ef]{
  position:relative;
  border-radius:0.25rem;
  transition:width .25s ease;
  animation:indeterminate-254ca3ef 2s ease infinite
}